
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    Time: cc.Label = null;

    stamp = 0;
    curTime = 0;
    mainCom = null;

	updateTimer(dt) {
		this.stamp += dt;
		if(this.stamp >= 1) {
			this.stamp -= 1;
			this.curTime --;
			this.Time.string = (this.curTime > 9 ? '' : '0') + this.curTime;
			if(this.curTime <= 0) {
				this.timeUp();				
			}
		}
	}

	timeUp() {
		// this.unschedule(this.updateTimer);
        this.scheduleOnce(()=>this.close(),0.5);
        this.mainCom.whenTimeup();
	}

	showTimer(time,mc) {
		this.close();
		this.node.active = true;
		this.Time.string = (time > 9 ? '' : '0') + time;
		this.curTime = time;
        this.mainCom = mc;
		this.stamp = 0;
		this.schedule(this.updateTimer, 0.1);
	}

	stopTimer(){
		cc.log('into time stop-----')
		this.unschedule(this.updateTimer);
	}

	resumeTimer(){
		cc.log('into time resume-----')
		this.schedule(this.updateTimer, 0.1);
	}

	close() {
		this.unschedule(this.updateTimer);
		this.node.active = false;
	}

    // update (dt) {}
}
