const { ccclass, property } = cc._decorator;

@ccclass
export default class WordTool extends cc.Component {

    @property({ type: cc.Sprite, displayName: 'word' }) sfWord: cc.Sprite = null;

    @property({ type: cc.SpriteFrame, displayName: 'banana' }) banana: cc.SpriteFrame = null;
    @property({ type: cc.SpriteFrame, displayName: 'bananas' }) bananas: cc.SpriteFrame = null;
    @property({ type: cc.SpriteFrame, displayName: 'coconut' }) coconut: cc.SpriteFrame = null;
    @property({ type: cc.SpriteFrame, displayName: 'coconuts' }) coconuts: cc.SpriteFrame = null;
    @property({ type: cc.SpriteFrame, displayName: 'mango' }) mango: cc.SpriteFrame = null;
    @property({ type: cc.SpriteFrame, displayName: 'mangoes' }) mangoes: cc.SpriteFrame = null;

    Init(img: cc.SpriteFrame,targetPos:cc.Vec2 = cc.v2(0,-100),callback:Function = null) {
        this.sfWord.node.opacity = 0;
        this.sfWord.node.setPosition(cc.v2(0,100));
        this.sfWord.node.active = true;
        this.sfWord.spriteFrame = img;
        this.unscheduleAllCallbacks();
       
        this.sfWord.node.stopAllActions();
        cc.tween(this.sfWord.node)
        .to(0.3,{position:cc.v3(targetPos.x,targetPos.y,0),opacity:255})
        .delay(2.4)
        .to(0.3,{opacity:0})
        .call(()=>{
            callback && callback();
        })
        .start();
    }

    
}
