import Util from "./Util";

/**
 * 音频类型枚举
 */
export enum AudioType {
  /** 音效 */
  AUDIO_EFFECT,
  /** 背景音乐 */
  BG_MUSIC,
}

/**
 * 音频管理类
 */
class AudioManager {
  private static instance: AudioManager;
  public static getInstance(): AudioManager {
    if (this.instance == null) {
      this.instance = new AudioManager();
    }
    return this.instance;
  }

  isPlaying = false; //是否播放中
  AUDIO_URL = "audios/";
  COMMON_AUDIO_URL = "audio_common/";
  RECORD = "record";
  GAME_END = "gameEnd"; //结束音效
  GAME_COIN_MID = "coin_mid";
  GAME_COIN_LARGE = "coin_large";
  LETS_SAY = "lets_say";
  TRY_AGAIN = "try_again";
  SUPER_JOB = "super_job";
  GAME_RIGHT = "game_right"; //正确音效
  GAME_ERROR = "game_error"; //错误音效
  LISTEN_AND_CHOOSE = "Listen_and_choose"; //引导音效
  _masterVolume: number = 1.0; //音量
  CHOOSE_THE_CORRECT_ANSWER = "choose_the_correct_answer"; //拖动引导
  CHOOSE_THE_CORRECT_WORD = "choose_the_correct_word";
  GAME_CLICK = "click";

  effectId = -1;
  audio_common = [
    "record",
    "gameEnd",
    "coin_mid",
    "coin_large",
    "lets_say",
    "try_again",
    "super_job",
    "game_right",
    "game_error",
    "Listen_and_choose",
    "choose_the_correct_answer",
    "choose_the_correct_word",
    "click",
    "jinbi1",
    "jinbi2",
    "jinbi3",
  ];

  playMusic(url: string, callBack?: Function, isPlaying: boolean = true) {
    this._masterVolume = url == this.GAME_END ? 0.6 : 1;
    let path;
    if (this.audio_common.indexOf(url) != -1) {
      path = this.COMMON_AUDIO_URL;
    } else {
      path = this.AUDIO_URL;
    }

    console.log(`加载的文件路径名---${path + url}`);
    cc.loader.loadRes(path + url, cc.AudioClip, (err, audio) => {
      if (err) {
        console.log(err, "音效加载失败===============>>");
      } else {
        this.loadMusic(audio, callBack, isPlaying);
      }
    });
  }

  loadMusic(audio: cc.AudioClip, callBack?: Function, isPlaying?: boolean) {
    this.isPlaying = isPlaying;
    cc.audioEngine.stop(this.effectId);
    this.effectId = cc.audioEngine.play(audio, false, this._masterVolume);
    cc.audioEngine.setFinishCallback(this.effectId, () => {
      Util.stopVoiceAni();
      callBack && callBack();
      this.isPlaying = false;
    });
  }

  /**
   * 播放音效音乐，完成时的回调支持回调和Promise,不会被之前的打断
   * @param url 播放的音效地址
   * @param loop 是否循环
   * @param cb 播放完成的回调
   */
  public async playEffect(url: string, loop = false, cb = () => {}) {
    const id = await this._play(url, AudioType.AUDIO_EFFECT, loop);
    cc.audioEngine.setFinishCallback(id, cb);
    return id;
  }

  /** 播放背景音效 */
  public async playBgMusic(url: string, loop = true, cb = () => {}) {
    const id = await this._play(url, AudioType.BG_MUSIC, loop);
    // cc.audioEngine.setFinishCallback(id, cb);
    return id;
  }

  /**
   * 播放音频
   * @param url 播放的音频地址
   * @param type 播放的类型，音效或者背景音乐
   * @param loop 是否循环
   */
  private _play(url: string, type: AudioType, loop: boolean): Promise<number> {
    return new Promise((resolve) => {
      // console.time('time_laodAudio');
      this._masterVolume = url == this.GAME_END ? 0.6 : 1;
      let path;
      if (this.audio_common.indexOf(url) != -1) {
        path = this.COMMON_AUDIO_URL;
      } else {
        path = this.AUDIO_URL;
      }

      // console.log(`加载的文件路径名---${path + url}`);
      cc.loader.loadRes(path + url, cc.AudioClip, (err, audio) => {
        if (err) {
          console.log(err, "音效加载失败===============>>");
        } else {
          // console.timeEnd('time_laodAudio')
          let id = -1;
          if (type === AudioType.AUDIO_EFFECT) {
            id = cc.audioEngine.playEffect(audio, loop);
          } else if (type === AudioType.BG_MUSIC) {
            id = cc.audioEngine.playMusic(audio, loop);
            cc.audioEngine.setMusicVolume(0.3);
          }
          resolve(id);
        }
      });
    });
  }
}

export default AudioManager.getInstance();
