import TigoManager from "../../Scripts/base/TigoManager";
import AudioManager from "../../Scripts/common/AudioManager";
import GameData from "../../Scripts/common/GameData";

const { ccclass, property } = cc._decorator;

@ccclass
export default class RepeatAfterMe extends cc.Component {

    @property({ type: cc.Node, displayName: "跟读显示" }) rcNode: cc.Node = null;

    private str_nowActionType: string = "";
    private num_wrongNum = 0;
    private bool_isRecordState = false;
    private str_gameWord: string;
    private str_sound: string;
    private num_index: number;
    onLoad() {

        cc.game.on(cc.game.EVENT_SHOW, () => {
            if (this.bool_isRecordState) {
                this.playWrong();
                this.bool_isRecordState = false;
            }
        });
    }

    onEnable() {
        window.webframe.Notify.listen("callCocos_data", this.getAppData, this, false);
    }

    onDisable() {
        window.webframe.Notify.ignore("callCocos_data", this.getAppData, this, false);
    }


    /**
     * 
     * @param str_gameWord 跟读文字
     * @param str_sound 跟读音频
     * @param num_index 跟读索引
     */
    prepareRecord(str_gameWord: string, str_sound: string, num_index: number = 1) {
        this.num_index = num_index;
        this.str_sound = str_sound;
        this.str_gameWord = str_gameWord;
        this.num_wrongNum = 0;
        this.str_nowActionType = "";
        this.rcNode.active = false
        AudioManager.playEffect('listen_and_say', false, () => {
            AudioManager.playEffect(this.str_sound, false, () => {
                this.record()
            })
        })
    }

    record() {
        console.log("record begin");
        this.str_nowActionType = "";
        AudioManager.playEffect('record')
        this.rcNode.active = false

        //callApp 开启录音功能 name：app存储录音文件的名字
        // this.recordItem = cc.instantiate(this.rcPrefab)
        this.rcNode.active = true
        // this.rcNode.addChild(this.recordItem);
        this.rcNode.getComponent("recordAin").initeAniFun(3);
        TigoManager.callAppToRecord(3000, this.str_gameWord);
        // gameState.isRecordState = true;
        //语音测试开关
        if (!CC_BUILD) {
            console.log("测试record");
            this.scheduleOnce(() => {
                // gameState.isRecordState = false;
                this.playRight(1);
            }, 2);
        }
    }

    getAppData(data) {
        console.log("offbus:getAppData  ====  " + JSON.stringify(data));
        var self = this;
        if (data.action == "TiGoRecordFinish") {
            if (this.str_nowActionType == "") {
                console.log("in TiGoRecordFinish!!!   " + this.str_nowActionType)
                this.str_nowActionType = data.action;
                this.rcNode.active = false
                // (<any>window)._TigoManager.callAppToResult(this.game_words[this.cur_index], data.param.path);
                TigoManager.callAppToResult(this.str_gameWord, data.param.path);
                var n = Number(GameData.record_num);
                n += 1;
                GameData.record_num = n.toString();
            }
        }
        else if (data.action == "TiGoVoiceEvaluationFinish") {
            if (this.str_nowActionType == "TiGoRecordFinish") {
                console.log("in TiGoVoiceEvaluationFinish!!!    " + this.str_nowActionType);
                this.str_nowActionType = data.action;
                // gameState.isRecordState = false;
                if (this.num_wrongNum >= 2) {
                    self.playRight(1);
                    return;
                }
                switch (Number(data.param.code)) {

                    case 200:
                        console.log("getAppData score ====  " + data.param.score);
                        if (Number(data.param.score) > 0) {
                            //单词的话 >0就是正确
                            var s = Number(data.param.score);
                            console.log("加金币 ======= " + s);
                            self.playRight(s);
                        }
                        else {
                            self.playWrong();
                        }
                        break;
                    default:
                        self.playWrong();
                        console.log("get error! error === " + data.param.error);
                        break;
                }
            }
        }
    }

    playRight(score) {

        AudioManager.playEffect(AudioManager.SUPER_JOB)
        window.webframe.Notify.trigger('follow_read_completed', score)
        let gid = GameData.gid[1][this.num_index];
        TigoManager.callAppToCollect(gid, 1004, score);
        //EventMng.emit(GameEvent.ANSWER_COMPLETED, this.num_wrongNum);
        this.num_wrongNum = 0;
    }

    playWrong() {
        this.num_wrongNum++;
        AudioManager.playEffect(AudioManager.TRY_AGAIN, false, () => {
            this.record();
        });
    }
}
