import TigoManager from "../base/TigoManager";

export default class Point {
    public static readonly POINT_GAME_START: string = "Start a game"
    public static readonly POINT_GAME_END: string = "Complete a game"
    public static readonly POINT_ROUND_START: string = "Start a round"
    public static readonly POINT_ROUND_END: string = "Complete a round"

    private static _instance: Point;
    private gameName: string = '';
    private commonParams: any;
    private totalRound: number;

    public static get instance(): Point {
        if (Point._instance == null) {
            Point._instance = new Point();
        }
        return Point._instance;
    }
    /**
     * 初始化埋点功能
     * 如果有两个游戏,则需要调用两次init方法分别设置游戏名与关卡数
     * @param gameName 游戏名
     * @param totalRound 总关卡数
     */
    public init(gameName: string, totalRound: number): void {
        this.gameName = gameName;
        this.totalRound = totalRound;
        let info = TigoManager.getAppInfo();
        cc.log('info:', info);
        if (info) {
            this.commonParams = {
                "Level_Name": info.levelName,
                "Week_Name": info.weekName,
                "Course_ID": info.lessonId,
                "Course_Name": info.lessonName,
                "Module_id": info.moduleId,
                "Module_Name": info.moduleName,
                "Game_name": this.gameName
            };
            cc.log('commonParams:', this.commonParams);
        }
    }

    /**
     * 遊戲開始
     * @param gameName 游戏名称
     */
    public gameStart(): void {
        this.callNative(Point.POINT_GAME_START, this.commonParams);
    }

    /**
     * 遊戲結束
     * @param gameName 
     */
    public gameEnd(): void {
        this.callNative(Point.POINT_GAME_END, this.commonParams);
    }

    /**
     * 回合结束
     * @param currentRound 当前关卡
     */
    public roundStart(currentRound: number): void {
        let obj: any = { ...this.commonParams };
        obj["Which round"] = `${currentRound}/${this.totalRound}`;
        this.callNative(Point.POINT_ROUND_START, obj);
    }

    /**
     * 回合结束
     * @param currentRound 当前关卡
     * @param times 总点击次数
     */
    public roundEnd(currentRound: number, times: number): void {
        let obj: any = { ...this.commonParams };
        obj["Which round"] = `${currentRound}/${this.totalRound}`;
        obj["Result "] = times;
        this.callNative(Point.POINT_ROUND_END, obj);
    }

    private callNative(event: string, data: any): void {
        cc.log(`event:${event},data:${JSON.stringify(data)}`);
        if (TigoManager.getAppInfo() && TigoManager.getAppInfo().levelName && TigoManager.getAppInfo().weekName) {
            TigoManager.callPoint(event, data);
        } else {
            cc.warn("原生参数为空，无法埋点");
        }
    }
}