import GameMenu from "./gameMenu"
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    quizLabel: cc.Label = null;

    @property(cc.Node)
    selecNode:cc.Node = null;

    @property(cc.Sprite)
    showSpr:cc.Sprite = null;

    @property(cc.Sprite)
    btnbg:cc.Sprite = null;

    @property(cc.Node)
    veilNode:cc.Node = null;

    @property(cc.Node)
    popenNdoe:cc.Node = null;

    @property(cc.Button)
    clickBut:cc.Button = null;

    @property(cc.Animation)
    udAni:cc.Animation = null;

    gameid = 0;
    curGid = 0;
    gMenu:GameMenu = null;
    isSec = 0;

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        
    }

    initInfo(gid,cid,isSec,gm){
        this.gameid = gid;
        this.curGid = cid;
        this.isSec = isSec;
        this.gMenu = gm;
        console.log('game gate id ='+gid);
        console.log('game isSec ='+this.isSec);
        if (this.gameid == this.curGid) {
            this.selecNode.active = true;
            this.veilNode.active = false;
            this.popenNdoe.active = true;
            this.udAni.play();
        }else if (this.gameid < this.curGid && this.isSec == 0) {
            this.veilNode.active = false;
            this.popenNdoe.active = true;
            this.clickBut.interactable = false;

            // let newMaterial = cc.Material.createWithBuiltin(<any>cc.Material.BUILTIN_NAME.GRAY_SPRITE,0)
            // this.showSpr.setMaterial(0, newMaterial);
            // this.btnbg.setMaterial(0, newMaterial);
            // this.quizLabel.setMaterial(0, newMaterial);
            this.showSpr.node.opacity = 190;
            this.btnbg.node.opacity = 190;
            this.quizLabel.node.opacity = 190;

        }else if (this.gameid > this.curGid) {
            this.clickBut.interactable = false;
        }

        if (this.isSec == 1) {
            this.veilNode.active = false;
            this.popenNdoe.active = true;
            this.clickBut.interactable = true;
        }

        this.setShowImage();
    }

    setShowImage(){//动态加载 游戏的展示图 按照游戏顺序命名图片sg1-4
        let url = ''; 
        if (this.gameid == 0) {
            url = 'gameshow/sg1';
            this.quizLabel.string  = 'Game 1';
        }else if(this.gameid == 1) {
            url = 'gameshow/sg2';
            this.quizLabel.string  = 'Game 2';
        }else if(this.gameid == 2) {
            url = 'gameshow/sg3';
            this.quizLabel.string  = 'Game 3';
        }else if(this.gameid == 3) {
            url = 'gameshow/sg4';
            this.quizLabel.string  = 'Game 4';
        }
        let self = this;
        cc.loader.loadRes(url,cc.SpriteFrame,function(err,spriteFrame) {
            self.showSpr.spriteFrame = spriteFrame;
            });
    }

    start () {

    }

    onClickBut(){
        this.gMenu.gateClick(this.gameid);
    }

    // update (dt) {}
}
