
const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    centerNode: cc.Node = null;

    @property({ type: [cc.Node], displayName: "右边动画组" })
    rightNode: cc.Node[] = [];

    @property({ type: [cc.Node], displayName: "右边动画组" })
    leftNode: cc.Node[] = [];


    // LIFE-CYCLE CALLBACKS:

     onLoad () {
        //  this,this.initeAniFun(3);
     }

    initeAniFun(sec) {
        cc.log('录音 sec == '+sec);
        let anim = this.centerNode.getComponent(cc.Animation);
        let clip = anim.currentClip || anim.defaultClip;
        if (clip) {
            clip.speed = sec * 0.1;
            anim.play();
        }
        let n = 0;
        this.schedule(() => {
            this.rightNode[n].getComponent(cc.Animation).play()
            this.leftNode[n].getComponent(cc.Animation).play()
            n += 1;
        }, 0.2, 4, 0.2);

    }

    start() {

    }

    // update (dt) {}
}
