import GameData from "./GameData";
// import TigoManager = require("../base/Tigo");
import MainScene from "./Main";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    gpList: cc.Node[] = [];

    @property(cc.Prefab)
    gatePre:cc.Prefab = null;

    curgameId = 0;
    isSecgame = 0;
    mainS:MainScene = null;
    canClick = true;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {
    // }

    initMenu(cid,issg,isGoon,ms){
        cc.log('game menu curid(page_num) == '+cid);
        this.curgameId = cid;//这个是先前就有的page_num;用它判断游戏进度
        this.isSecgame = issg;
        this.mainS = ms;

        let copygpList:cc.Node[];
        let gameNum = GameData.node_path.length;
        if (gameNum == 2) { //根据gamedata中配置的游戏数量显示
            copygpList = [this.gpList[1],this.gpList[2]]
        }else if(gameNum == 4){
            copygpList = [...this.gpList];
        }else if(gameNum == 3){
            copygpList = [this.gpList[0],this.gpList[1],this.gpList[2]]
            for (let i = 0; i < copygpList.length; i++) {
                copygpList[i].x += 200;
            }
            copygpList[0].y += 50;
            copygpList[1].y -= 30;
            copygpList[2].y -= 40;
        }

        for (let i = 0; i < copygpList.length; i++) {
            let gateitem = cc.instantiate(this.gatePre);
            copygpList[i].addChild(gateitem);
            let dtgCom = gateitem.getComponent('gameGate');
            dtgCom.initInfo(i,this.curgameId,this.isSecgame,this);//由0开始给 游戏赋值ID 对应 gamedate的node_path
        }

        if (isGoon && issg == 0) {
            this.scheduleOnce(()=>this.autoGame(),2);
        }
    }

    start () {
        
    }

    autoGame(){
        cc.log('自动进入 gameid = '+this.curgameId);
        this.mainS.openGame(this.curgameId);
    }

    gateClick(gid){
        cc.log('点击进入 gameid = '+gid);
        this.mainS.openGame(gid);
        
        
    }

    // update (dt) {}
}
