
cc.Class({
    extends: cc.Component,

    properties: {
        screenshotsprint: {
            type: cc.Sprite,
            default: null,
        },
        camera: cc.Camera,
    },

    screenshots(callback) { //截图函数
        let picData = this.initImage();
        let spriteFrame = this.showSprite(picData);
        if (callback) callback(spriteFrame);
    },
    screenshotsInti() {//截图初始化
        this._width = 0;
        this._height = 0;
        let texture = new cc.RenderTexture();
        let gl = cc.game._renderContext;
        texture.initWithSize(cc.visibleRect.width, cc.visibleRect.height, gl.STENCIL_INDEX8);
        this.camera.targetTexture = texture;
        this.texture = texture;
    },
    initImage() {
        let data = this.texture.readPixels();
        this._width = this.texture.width;
        this._height = this.texture.height;
        let picData = this.filpYImage(data, this._width, this._height);
        return picData;
    },

    showSprite(picData) {
        let texture = new cc.Texture2D();
        console.log('canvas', this._width, this._height);
        //cc.Texture2D.RGBA8888是一个cc.Texture2D中的枚举，可以自行查阅官网API
        texture.initWithData(picData, cc.Texture2D.RGBA8888, this._width, this._height);
        let spriteFrame = new cc.SpriteFrame();
        spriteFrame.setTexture(texture);
        return spriteFrame;
    },
    filpYImage(data, width, height) {
        // create the data array
        let picData = new Uint8Array(width * height * 4);
        let rowBytes = width * 4;
        for (let row = 0; row < height; row++) {
            let srow = height - 1 - row;
            let start = srow * width * 4;
            let reStart = row * width * 4;
            // save the piexls data
            for (let i = 0; i < rowBytes; i++) {
                picData[reStart + i] = data[start + i];
            }
        }
        return picData;
    },

    captureAction(capture) {
        let width = cc.winSize.width;
        let height = cc.winSize.height;
        let scaleAction = cc.scaleTo(1, 0.5);
        let targetPos = cc.v2(width / 2 - 200, height / 2 - 200);
        let moveAction = cc.moveTo(1, targetPos);
        let spawn = cc.spawn(scaleAction, moveAction);

        let finished = cc.callFunc(() => {
            // capture.destroy();
            capture.getComponent(cc.Sprite).spriteFrame = null;
            console.log('动画结束');
        })
        let action = cc.sequence(spawn, finished);
        capture.runAction(action);
    },
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {

    },

    testToCapture(){
        this.camera.node.active = true;
        // this.node.position = cc.v2(549, 211);
        this.screenshotsInti();//首先进行初始化
        this.scheduleOnce(() => {
                //spriteFrame可以用于替换精灵中的spriteFrame属性来显示截图
                this.screenshots((spriteFrame)=>{ 
                this.screenshotsprint.spriteFrame=spriteFrame
                // this.captureAction(this.screenshotsprint.node);
                this.camera.node.active = false;
                });
            }
            , 0);//等到下一帧再进行截图的操作
    },

    testIoCapture(){
        
    },

    // update (dt) {},
});