import AudioManager from "./AudioManager"

const { ccclass, property } = cc._decorator;
@ccclass
class Util extends cc.Component {

    @property(cc.Prefab)
    coin_small: cc.Prefab = null;

    @property(cc.Prefab)
    coin_mid: cc.Prefab = null;

    @property(cc.Prefab)
    coin_large: cc.Prefab = null;


    private static instance: Util;
    public static getInstance(): Util {
        if (this.instance == null) {
            this.instance = new Util();
        }
        return this.instance;
    }
    PrefabNode: cc.Node = null

    voiceNode: cc.Node = null;
    voice_name = "prefab_common/voice";

    node_name = "prefab_common/startLayer";
    coin_name_1 = "prefab_common/coin_small";
    coin_name_2 = "prefab_common/coin_mid";
    coin_name_3 = "prefab_common/coin_large";
    exitbutton = "prefab_common/exitbutton";

    onLoad() {
        cc.log(this.coin_small);
    }

    addVoice(node: cc.Node, callBack: Function, that: any) {
        cc.loader.loadRes(this.voice_name, cc.Prefab, (err, Prefab) => {
            if (err) {
                console.log(err, "Voice 加载失败===============>>")
            } else {
                let voice = cc.instantiate(Prefab)
                let clickBack = ()=>{
                    callBack && callBack.bind(that)()
                }
                voice.on('click', clickBack, that);
                node.addChild(voice,996)
                voice.name = "voices"
                this.voiceNode = voice
            }
        });
    }

    addExitbutton(node: cc.Node, callBack: Function, that: any) {
        cc.loader.loadRes(this.exitbutton, cc.Prefab, (err, Prefab) => {
            if (err) {
                console.log(err, "exitbutton 加载失败===============>>")
            } else {
                let exitbutton:cc.Node = cc.instantiate(Prefab)
                let clickBack = ()=>{
                    // TigoManager.callAppToCollect(1,1002,1);
                    callBack && callBack.bind(that)()
                }
                let but = exitbutton.getChildByName('exbt');
                but.on('click', clickBack, that);
                node.addChild(exitbutton,996)
            }
        });
    }
    
    // stopVoiceAni(){
    //     this.voiceNode.getComponent(cc.Animation).play("voice_end")
    // }

    // playVoiceAni(){
    //     this.voiceNode.getComponent(cc.Animation).play("voice")
    // }

    stopVoiceAni(){
        if(this.voiceNode)
        {
            this.voiceNode.getComponent(cc.Animation).play("voice_end")
        }
    }

    playVoiceAni(){
        if(this.voiceNode)
        {
            this.voiceNode.getComponent(cc.Animation).play("voice")
        }
    }

    addStartLayer(node: cc.Node, callBack: Function, that: any) {
        cc.loader.loadRes(this.node_name, cc.Prefab, (err, Prefab) => {
            if (err) {
                console.log(err, "startNode 加载失败===============>>")
            } else {
                this.PrefabNode = cc.instantiate(Prefab)
                this.setStartBtnBack(callBack, that)
                node.addChild(this.PrefabNode,999)
            }
        });
    }

    setStartBtnBack(callBck: Function, that: any) {
        let btn = this.PrefabNode.getChildByName("start")
        let clickBack = () => {
            this.PrefabNode.active = false
            callBck && callBck.bind(that)()
        }
        btn.on('click', clickBack, that);
    }

    addCoin_small(node: cc.Node, index: number) {

        cc.loader.loadRes(this.coin_name_1, cc.Prefab, (err, Prefab) => {
            if (err) {
                console.log(err, "prefab 加载失败===============>>")
            } else {
                let coin = cc.instantiate(Prefab)
                node.addChild(coin,999)
                let spine = coin.getChildByName("spineNode").getComponent(sp.Skeleton);
                let sc = coin.getComponent("coins");
                sc.initNum(index);
                spine.clearTracks();
                spine.setAnimation(0, "animation", false)
                spine.setCompleteListener(() => {
                    coin.destroy();
                })
            }
        });

    }

    //一个新的游戏结束流程 通知回到game menu
    addCoin_midAndMenu(node: cc.Node,et) {
        cc.loader.loadRes(this.coin_name_2, cc.Prefab, (err, Prefab) => {
            if (err) {
                console.log(err, "prefab 加载失败===============>>")
            } else {
                let coin = cc.instantiate(Prefab)
                node.parent.addChild(coin,999)
                coin.name = "coin"
                let spine = coin.getChildByName("spineNode").getComponent(sp.Skeleton);
                spine.clearTracks();
                spine.setAnimation(0, "Animation", true)
                AudioManager.playEffect(AudioManager.GAME_COIN_LARGE,false,()=>{
                    coin.destroy();
                    window.webframe.Notify.trigger("entryMenu",et);
                    cc.log(Date.now());
                })
            }
        });
    }
}
export default Util.getInstance()