/**
 * 消息中心
 * 网络消息和客户端消息都从这里注册
 * @class
 * @extends cc.Class
 */
let LogFilterArray = ['EVENT_TIME_ONE_SECOND', 'EVENT_FRIEND_OFFLINE_TIME_CHANGE'];
class NotifierCenter {
    constructor() {
        let key = arguments[0];
        this._TAG = '[' + key + '/NotifierCenter]';
        this.events = {};
        this.launch();
    }

    getScopeName(scope) {
        try {
            if (scope) {
                return (scope._TAG ? scope._TAG : (scope.name ? scope.name : "unknow")).toString();
            } else {
                return "unknow";
            }
        } catch (e) {
            return "unknow";
        }
    }

    listen(eventName, handler, scope, isOnce) {
        if (typeof eventName != 'string' || typeof handler !== 'function') {
            cc.log(this._TAG, ' listen err: eName :' + eventName + 'handler:' + handler);
            return;
        }
        var observer = { handler: handler, scope: scope, isOnce: isOnce };
        var observers = this.events[eventName] || [];
        for (var i = 0; i < observers.length; i++) {
            var obs = observers[i];
            //func.toString() 判断匿名函数的情况
            if (obs.scope == scope && (obs.handler == handler || obs.handler.toString() == handler.toString())) {
                cc.log(this._TAG, '消息重复注册:' + eventName + ' function:' + handler.name);
                return;
            }
        }
        observers.push(observer);
        this.events[eventName] = observers;
    }

    ignore(eventName, handler, scope) {
        if (typeof eventName != 'string') {
            cc.log("-----eventName----" + eventName);
            this.ignoreScope(arguments[0]);
            return;
        }
        if (typeof eventName != 'string' || typeof handler != 'function') {
            cc.log(this._TAG, 'NotifierCenter remove err: eName :' + eventName + 'scope:' + scope._TAG);
            return;
        }

        var observers = this.events[eventName];
        if (!observers || observers.length === 0) {
            return;
        }
        scope = scope || this;
        var idx = -1; //同一个消息可能有多个节点注册
        for(var i =0; i < observers.length;++i){
            var observer = observers[i];
            if (observer.scope == scope && (observer.handler == handler || observer.handler.toString() == handler.toString())) {
                idx = i;
            }
        }
        if(idx > -1){
            this.events[eventName].splice(idx,1);
        }
        // var self = this;
        // this.events[eventName] = observers.filter(function (obs) {
        //     if (typeof obs != 'undefined') {
        //         return obs.scope != scope || obs.handler != handler;
        //     }
        // });
    }

    trigger(eventName, params) {
        if (!this.isRunning) { return; }
        if (typeof eventName != 'string') {
            cc.log(this._TAG, ' trigger err: eventName :' + eventName);
            return;
        }
        var keys = Object.keys(this.events);
        var obs = this.events[eventName];
        if (!obs) {
            return;
        }
        // if(CC_DEBUG && -1 == LogFilterArray.indexOf(eventName)){
        //     cc.log(this._TAG,'trigger '+eventName+', observers: '+obs.length);
        // }
        var args = Array.prototype.slice.call(arguments, 1);
        var self = this;
        //发送
        for (let i = 0; i < obs.length; i++) {
            let ob = obs[i];
            if (ob.scope && ob.scope instanceof cc.Component && !cc.isValid(ob.scope.node)) {
                continue;
            }
            ob.handler.apply(ob.scope, args);
            if (ob.isOnce) {
                cc.log(self._TAG, 'trigger and remove once listener', eventName);
            }
        }
        //移除 once和不合法的
        obs = this.events[eventName];
        if (!obs) {
            return;
        }
        this.events[eventName] = obs.filter(function (ob) {
            if (ob.scope && ob.scope instanceof cc.Component && !cc.isValid(ob.scope.node)) {
                return false;
            }
            return !ob.isOnce;
        });
    }

    ignoreScope(scope) {
        let ignoreMsgs = ":";
        for (var msg in this.events) {
            var obs = this.events[msg];
            if (obs) {
                this.events[msg] = obs.filter(function (ob) {
                    if (ob.scope != scope) {
                        return true;
                    } else {
                        ignoreMsgs = ignoreMsgs + msg + ',';
                        return false;
                    }
                });
            }
        }
    }
    destroy() {
        cc.log(this._TAG, 'destroy');
        this.events = {};
    }
    shut() {
        cc.log(this._TAG, '--------shut');
        this.isRunning = false;
    }
    launch() {
        cc.log(this._TAG, '--------launch');
        this.isRunning = true;
    }
}
module.exports = NotifierCenter;

