
const { ccclass, property } = cc._decorator;
// import Tigo = require("../base/TigoManager");
import Tigo from "../base/TigoManager"
import GameData from "./GameData"
import Util from "./Util";

if (CC_EDITOR) {

    // 重写update方法 达到在编辑模式下 自动播放动画的功能
    sp.Skeleton.prototype['update'] = function (dt) {
        if (CC_EDITOR) {
            cc['engine']._animatingInEditMode = 1;
            cc['engine'].animatingInEditMode = 1;
        }
        if (this.paused) return;

        dt *= this.timeScale * sp['timeScale'];

        if (this.isAnimationCached()) {

            // Cache mode and has animation queue.
            if (this._isAniComplete) {
                if (this._animationQueue.length === 0 && !this._headAniInfo) {
                    let frameCache = this._frameCache;
                    if (frameCache && frameCache.isInvalid()) {
                        frameCache.updateToFrame();
                        let frames = frameCache.frames;
                        this._curFrame = frames[frames.length - 1];
                    }
                    return;
                }
                if (!this._headAniInfo) {
                    this._headAniInfo = this._animationQueue.shift();
                }
                this._accTime += dt;
                if (this._accTime > this._headAniInfo.delay) {
                    let aniInfo = this._headAniInfo;
                    this._headAniInfo = null;
                    this.setAnimation(0, aniInfo.animationName, aniInfo.loop);
                }
                return;
            }

            this._updateCache(dt);
        } else {
            this._updateRealtime(dt);
        }
    }
}
@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Prefab)
    menuPre:cc.Prefab = null;

    menuItem:cc.Node = null;

    node_nowShow: cc.Node = null;
    page_num = 0
    // isL5 = false
    Lesson_Sign = "L1W1D5Game"
    page = "page"
    star = "star"
    record_num = "record_num"

    isSecgame = 0;//新加属性 用于判断是否已经玩过最后一个游戏了
    secKey = "seckey";

    onLoad() {
        let page = this.Lesson_Sign + this.page
        let star = this.Lesson_Sign + this.star
        let record_num = this.Lesson_Sign + this.record_num

        //重连后获取开口数 
        GameData.record_num = Tigo.callAppToStorage("GET", record_num) || 0;

        //获得金币数量
        let num = Tigo.callAppToStorage("GET", star) || 0;
        //本地设置金币数量
        GameData.setStarNum(num);
        console.log("当前游戏开口数===========》" + GameData.record_num);

        //获取游戏进度
        this.page_num = Tigo.callAppToStorage("GET", page) || this.page_num
        console.log("当前游戏页数============》" + this.page_num);
        //获取通关tag,用于menu
        let  sgk = this.Lesson_Sign + this.secKey;
        this.isSecgame = Tigo.callAppToStorage("GET", sgk) || 0;
        console.log("游戏 isSec ===" + this.isSecgame);
        if (Tigo.callAppToStorage("GET", sgk)) {
            console.log("callAppToStorage sgk issecgame 有数据");
        }

        /* 取消多点触控 */
        cc.macro.ENABLE_MULTI_TOUCH = false;

        //通知app加载完成
        Tigo.callAppToLoaded();
        
        //this.openMenu(); //新加入的 先进入game menu 方法
        this.openGame(0);
        this.registerListener();

        cc.game.on(cc.game.EVENT_HIDE, function () {
        console.log("游戏进入了后台~~~~~~~~");
        cc.audioEngine.pauseMusic();
        cc.audioEngine.pauseAllEffects();
        cc.game.pause();
        });

        cc.game.on(cc.game.EVENT_SHOW, function () {
        console.log("重新回到了游戏~~~~~~~~");
        cc.audioEngine.resumeMusic();
        cc.audioEngine.resumeAllEffects();
        cc.game.resume();
        });
    }

    start() {
        
    }

    registerListener() {
        // webframe.Notify.listen("chagePage", this.chagePage, this, false);
        window.webframe.Notify.listen("entryMenu", this.entryMenu, this, false);
    }

    entryMenu(endtag){//游戏结束时调用的 打开menu 方法
        this.page_num++;
        if (this.page_num >= GameData.node_path.length-1) {
            Tigo.callAppToSpeed();
        }
        console.log("this.page_num ===" + this.page_num);
        console.log("GameData.node_path.length ===" + GameData.node_path.length);
        if (this.page_num >= GameData.node_path.length) {
            let  sgk = this.Lesson_Sign + this.secKey;
            Tigo.callAppToStorage("PUT", sgk, 1); //玩过最后一个游戏了
            this.isSecgame = 1;
            if (endtag) {
                console.log('into all game end ani');
                this.gameAllend();
                return;
            }
            this.page_num = GameData.node_path.length - 1;
        }
        ////////////////////////////////////////////////
        //之前的chagepage时要设置的 属性
        let page = this.Lesson_Sign + this.page
        let star = this.Lesson_Sign + this.star
        let record_num = this.Lesson_Sign + this.record_num;
        Tigo.callAppToStorage("PUT", page, this.page_num);
        Tigo.callAppToStorage("PUT", star, GameData.totalStar_num);
        Tigo.callAppToStorage("PUT", record_num,Number(GameData.record_num));
        ////////////////////////////////////////////////

        if (this.node_nowShow) {
            Util.voiceNode = null;
            this.node_nowShow.destroy()
        }
        cc.audioEngine.stopAllEffects();

        this.openMenu(true);
    }

    openMenu(isGoon:boolean = false){
        this.menuItem = cc.instantiate(this.menuPre);
        this.node.addChild(this.menuItem);
        this.menuItem.getComponent('gameMenu').initMenu(this.page_num,this.isSecgame,isGoon,this);
    }

    openGame(gid){//由菜单点击 进入游戏
        this.menuItem && this.menuItem.destroy(); //销毁menu
        this.loadPage(gid); //调用已有laodpage 方法
    }

    gameAllend(){//先前chagepage中的数据
        let star = this.Lesson_Sign + this.star;
        console.log("本部分一共获得金币 === " + GameData.totalStar_num)
        console.log("本部分一共开口 === " + GameData.record_num + "次");
        var h = {
            "userId": Tigo.userId,
            "lessonId": Tigo.classId,
            "redVal": GameData.totalStar_num,
            "openVal": GameData.record_num,
            "lessonModelId": Tigo.lessonModuleId
        }
        // Tigo.callAppToHttp("api/reward/info", "POST", h);
        Tigo.callAppToHttp("POST",GameData.totalStar_num,GameData.record_num);
        Tigo.callAppToStorage("remove", star);
        Tigo.callAppToClose("replayOrNext", true);    
    }

    loadPage(page_num) {
        console.log("当前加载的游戏页数--------------->" + page_num)
        let nameProfab_str = "prefabs/" + GameData.getNodePath(page_num)
        cc.loader.loadRes(nameProfab_str, (err, prefab) => {
            if (!err) {
                this.addPrefab(prefab);
            } else {
                console.log(err, "err 预制体加载失败==========>");
            }
        })
    }

    addPrefab(prefab) {
        if (this.node_nowShow) {
            Util.voiceNode = null;
            this.node_nowShow.destroy()
        }
        cc.audioEngine.stopAllEffects();
        this.node_nowShow = cc.instantiate(prefab);
        if (this.page_num != 0) {
            this.node_nowShow.opacity = 100
            cc.tween(this.node_nowShow)
                .to(1, { opacity: 255 })
                .start()
        }
        this.node.addChild(this.node_nowShow);

        if (this.node.getChildByName("coin")) {
            this.node.getChildByName("coin").destroy()
        }
    }

    onDestroy() {
        this.unRegisterListener();
    }

    unRegisterListener() {
        // webframe.Notify.ignore("chagePage", this.chagePage, this, false);
        window.webframe.Notify.ignore("entryMenu", this.entryMenu, this, false);
    }
}

