
const {ccclass, property} = cc._decorator;

@ccclass
export default class coinBoard extends cc.Component {

    @property(cc.Node)
    coin:cc.Node[]=[];    

    // onLoad () {}

    start () {

    }

    setCoinAni(num){
        for (let i = 0; i < this.coin.length; i++) {
            if (i<num) {
                this.coin[i].active = true;
            }else{
                this.coin[i].active = false;
            }
            
        }
        this.node.getComponent(cc.Animation).play();
        this.playCoinEffect(num);
        this.scheduleOnce(()=>{
            for (let i = 0; i < this.coin.length; i++) {
                this.coin[i].active = false;
            }
        },2);
    }

    playCoinEffect(num){
        let url = ''
        url = `jinbi${num}`
        cc.loader.loadRes("audio_common/"+url, cc.AudioClip, (err, audio)=>
        {
            if(err)
            {
                console.log(err,"金币音效加载失败===============>>")
            }else{
                cc.audioEngine.play(audio, false, 1);
            } 
        });
    }

    // update (dt) {}
}
