

const { ccclass, property } = cc._decorator;

enum HandSpineAnimationName {
    danji = "danji",
    dianji = 'dianji',
    taishou = 'taishou',
}

enum ApertureAnimationName {
    dianji = 'dianji',
    taishou = 'taishou'
}

@ccclass
export default class HandCtr extends cc.Component {

    @property({ type: sp.Skeleton, displayName: '光圈' }) skAperture: sp.Skeleton = null;
    @property({ type: sp.Skeleton, displayName: '手指' }) skHand: sp.Skeleton = null;

    start() {
        // this.Press();

        // this.scheduleOnce(() => { this.Lift(); }, 5);
        //this.Click();
        //this.ClickCycle();

       // this.TouchCycle(cc.v2(-100, -200), cc.v2(100, 200));gong
    }

    onEnable() {
        this.skAperture.node.active = false;
    }

    onDisable() {
        this.unscheduleAllCallbacks();
    }

    /* 按下 */
    private Press(callback: Function) {
        this.skAperture.node.active = false;
        this.playSpine(this.skHand, HandSpineAnimationName.danji, false, () => {
            this.skAperture.node.active = true;
            this.playSpine(this.skAperture, ApertureAnimationName.dianji, false, () => {
                callback && callback();
            });
        });
    }

    /* 抬起 */
    private Lift(callback: Function) {
        this.playSpine(this.skHand, HandSpineAnimationName.taishou, false, () => {
            callback && callback();
        });
    }

    /* 点击 */
    Click(callback: Function = null) {
        this.skAperture.node.active = true;
        this.playSpine(this.skAperture, ApertureAnimationName.dianji, false);
        this.playSpine(this.skHand, HandSpineAnimationName.danji, false, () => {
            this.playSpine(this.skHand, HandSpineAnimationName.taishou, false, () => {
                callback && callback();
            });
        });
    }

    /* 点击循环 */
    ClickCycle() {
        this.schedule(() => {
            this.Click();
        }, 2,cc.macro.REPEAT_FOREVER,0.01);
    }

    TouchAnimation(starPos: cc.Vec2, endPos: cc.Vec2, time: number = 0.5) {
        this.node.stopAllActions();
        this.node.setPosition(starPos);
        this.Press(()=>{
            cc.tween(this.node)
            .to(time, { position: cc.v3(endPos.x, endPos.y, 0) })
            .call(()=>{
                this.Lift(null)
            })
            .start();
        });
    }
    /**
     * 拖拽循环
     * @param starPos 开始位置
     * @param endPos 结束位置
     * @param waitTime 上一次与下一次的运行间隔
     * @param runTime 运行花费的时间
     */
    TouchCycle(starPos: cc.Vec2, endPos: cc.Vec2,waitTime: number = 3,runTime:number = 0.5, ) {
        this.schedule(() => {
            this.TouchAnimation(starPos, endPos, runTime);
        },waitTime,cc.macro.REPEAT_FOREVER,0.01);
    }

    private playSpine(spNode: sp.Skeleton, spName: string, loop: boolean = false, callback = null) {
        spNode.clearTracks();
        spNode.setToSetupPose();
        let track = spNode.setAnimation(0, spName, loop);
        if (track && !loop) {
            spNode.setCompleteListener((trackEntry, loopCount) => {
                let name = trackEntry.animation.name ? trackEntry.animation.name : "";
                if (name == spName && callback) {
                    callback();
                }
            });
        }
    }

}
