/**
 * TigoManager 原生交互 服务端接口管理
 * by lpw  2021/08/18
 */
class TigoManager {
	static _instance: TigoManager = null;
	static getInstance() {
		if (!this._instance) {
			this._instance = new TigoManager();
			this._instance.getBaseInfo()
		}
		console.log("TigoManager _instance is success");
		return this._instance;
	}

	userId = "";           //用户ID
	classId = "";          //课程ID
	moduleId = "";         //模块ID
	lessonModuleId = "";   //课程模块ID
	manageMentIndex = 1    //打点游戏下标(考虑到如果动这块肯定会动包 对应模块可自行设计)
	packageId = "";			//课包ID
	nextLessonModelId = ""	//下个课程模块ID
	nextType = ""			//下个模块类型

	//打点类型
	//接口文档地址	https://pplingo.atlassian.net/wiki/spaces/ED/pages/129598934
	ManageType = {
		CocosIn: 1001,      //进入Cocos
		CocosOut: 1002,     //退出Cocos
		gameLink: 1003,     //游戏环节对错 
		gameScore: 1004,    //游戏环节得分(例如语音识别得分)
		gameEvent: 1005,    //操作事件(没有对错的游戏称为操作事件)
	}

	//打点 result
	ManageResult = {
		CocosIn: 0,
		CocosOut: 0,
		gameScore: 0,
		gameLink_Right: 1,   	//游戏环节正确
		gameLink_Error: 0,		//游戏环节错误
		gameEvent: 1,    		//操作事件(没有对错的游戏称为操作事件)
	}

	//上报接口api类型
	//上报统计文档地址	https://pplingo.atlassian.net/wiki/spaces/ED/pages/129598934
	//上报进度文档地址	https://pplingo.atlassian.net/wiki/spaces/ED/pages/33785543/APP
	ReportType = {
		report_reward: "api/reward/info",      		//上报金币
		report_speed: "api/course/speed",    		//上报进度 
		report_operate: "api/operate/infoV2"  		//游戏打点
	}


	//cocos 与 native 通信 action 
	//接口文档地址 https://pplingo.atlassian.net/wiki/spaces/ED/pages/207455354
	CocosByNativeAction = {
		TiGoHttp: "TiGoHttp",                        				//一切请求服务器的行为标志
		TiGoClose: "TiGoClose", 									//cocos 关闭native界面
		replayOrNext: "replayOrNext",								//跳转到重播和下一课程界面
		TiGoStorage: "TiGoStorage",									//cocos调用native存储接口
		TiGoBaseInfo: "TiGoBaseInfo",								//获取用户基本信息
		TiGoLifecycle: "TiGoLifecycle",								//通知native cocos加载完成
		TiGoStopRecord: "TiGoStopRecord",							//通知native录音结束
		TiGoStartRecord: "TiGoStartRecord",							//通知native录音开始
		TiGoRecordFinish: "TiGoRecordFinish",						//native通知cocos录音完成可发起语音测评接口
		TiGoVoiceEvaluation: "TiGoVoiceEvaluation",					//cocos向native发起语音测评 
		TiGoVoiceEvaluationFinish: "TiGoVoiceEvaluationFinish", 	//语音测评结果
		TiGoAnalyse: "TiGoAnalyse"									//cocos统计分析记录（新的）
	}

	//设置用户基本信息 new此类时自动调用
	setBaseInfo(userData) {
		console.log("setBaseInfo ====== " + userData);
		let json = JSON.parse(userData);
		if (json) {
			this.setUserId(json.userId);
			this.setClassId(json.lessonId);
			this.setModuleId(json.moduleId);
			this.setLessonModuleId(json.lessonModuleId);
			this.setPackageId(json.packageId);
			this.setNextLessonModelId(json.nextLessonModelId);
			this.setNextType(json.nextType);
		}
	}

	setClassId(cid) {
		if (!cid) return
		this.classId = cid;
	}

	setUserId(uid) {
		if (!uid) return
		this.userId = uid;
	}

	setModuleId(mid) {
		if (!mid) return
		this.moduleId = mid;
	}

	setLessonModuleId(lid) {
		if (!lid) return
		this.lessonModuleId = lid;
	}

	setPackageId(pid) {
		if (!pid) return
		this.packageId = pid;
	}

	setNextLessonModelId(pid) {
		if (!pid) return
		this.nextLessonModelId = pid;
	}

	setNextType(pid) {
		if (!pid) return
		this.nextType = pid;
	}

	//发送close消息给app 
	callAppToClose(data, next = false) {

		var handleData = next ?
			{
				"action": this.CocosByNativeAction.TiGoClose,
				"param": {
					"closeNext": data
				}
			} :
			{
				"action": this.CocosByNativeAction.TiGoClose,
				"param": {

				}
			}
		console.log("callAppToClose ============== " + JSON.stringify(handleData));
		this.getNative(handleData)
	}

	//开启录音
	callAppToRecord(sec, word) {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoStartRecord,
			"param": {
				"name": "record",
				"time": sec.toString(),
				"standardText": word
			}
		}
		this.getNative(handleData)
		console.log("callAppToRecord ============== " + JSON.stringify(handleData));
	}

	//关闭录音
	callAppToStopRecord() {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoStopRecord,

		}
		this.getNative(handleData)
		console.log("callAppToStopRecord ============== " + JSON.stringify(handleData));
	}

	//语音测评
	callAppToResult(word, path) {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoVoiceEvaluation,
			"param": {
				"standardText": word || "",
				"path": path
			}
		}
		this.getNative(handleData)
		console.log("callAppToResult ============== " + JSON.stringify(handleData));
	}

	//存储
	callAppToStorage(method, key, val = 0) {
		var value = "";
		switch (method) {
			case "PUT":
				value = val.toString()
				break;
		}
		var handleData = {
			"action": this.CocosByNativeAction.TiGoStorage,
			"param": {
				"method": method,// get put clear 
				"key": key,
				"value": value.toString()
			}
		}
		console.log("callAppToStorage ============== " + JSON.stringify(handleData));
		if (window.tiGo) {
			var page_num = window.tiGo.callApp(JSON.stringify(handleData));
			return page_num;
		}
		if (window.webkit) {
			// window.webkit.messageHandlers.callApp.postMessage(JSON.stringify(handleData));
			var page_num = window.prompt("callApp", JSON.stringify(handleData));
			console.log(page_num);
			return page_num;
		}
	}

	//一切请求服务器的行为 api：接口 ，mehtod：put get post， params：接口所需参数 ，unique：交互app标识
	callAppToHttp(method, coin, speak) {
		let u = this.userId;
		let l = this.classId;
		let lM = this.lessonModuleId;
		console.log("userId ====== " + u + "   lessonId ====== " + l + "    lessonModuleId ===== " + lM)
		var h = {
			"userId": u,
			"lessonId": l,
			"lessonModelId": lM,
			"redVal": coin,
			"openVal": speak

		}
		var handleData = {
			"action": this.CocosByNativeAction.TiGoHttp,
			"param": {
				"url": this.ReportType.report_reward,
				"method": method,
				"params": h,
				"unique": this.classId
			}
		}
		this.getNative(handleData)
		console.log("callAppToHttp ============== " + JSON.stringify(handleData));
	}

	callAppToLoaded() {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoLifecycle,
			"param": {
				"name": "loaded",
				"param": ""
			}
		}
		this.getNative(handleData)
		console.log("callAppToLoaded ============== " + JSON.stringify(handleData));
	}

	//获取用户信息 返回值 {}
	getBaseInfo() {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoBaseInfo,
		}
		console.log("getBaseInfo ============== " + JSON.stringify(handleData));
		if (window.tiGo) {
			var BaseInfo = window.tiGo.callApp(JSON.stringify(handleData));
			this.setBaseInfo(BaseInfo)
			console.log(BaseInfo);
			console.log("tiGo BaseInfo ==== " + BaseInfo);
			return BaseInfo;

		}
		if (window.webkit) {
			// window.webkit.messageHandlers.callApp.postMessage(JSON.stringify(handleData));
			var BaseInfo = window.prompt("callApp", JSON.stringify(handleData));
			this.setBaseInfo(BaseInfo)
			console.log("webkit BaseInfo ==== " + BaseInfo);
			return BaseInfo;
		}
	}

	getAppInfo(): { userId: string, lessonId: string, lessonModuleId: string, moduleId: string, packageId: string, nextLessonModelId: string, nextType: string, gameId: string, propId: string, token: string, moduleName: string, lessonName: string, weekName: string, levelName: string, versionName: string } {
		let handleData = { "action": this.CocosByNativeAction.TiGoBaseInfo };
		let info = null;
		if (window.tiGo) {
			info = window.tiGo.callApp(JSON.stringify(handleData));
			return JSON.parse(info);
		} else if (window.webkit) {
			info = window.prompt("callApp", JSON.stringify(handleData));
			return JSON.parse(info);
		}
		return null;
	}

	callAppToCollect(gameIndex, type, result) {
		var h = {
			"userId": this.userId,
			"lessonId": this.classId,
			"lessonModelId": this.lessonModuleId,
			"gameIndex": gameIndex,
			"type": type,
			"result": result,
			"timestamp": this.getTime()
		}
		var handleData = {
			"action": this.CocosByNativeAction.TiGoHttp,
			"param": {
				"url": this.ReportType.report_operate,
				"method": "POST",
				"params": h
			}
		}
		this.getNative(handleData)
		console.log("callAppToCollect ============== " + JSON.stringify(handleData));
	}

	getTime() {
		var time = Date.now().toString();//获取到毫秒的时间戳，精确到毫秒
		time = time.substr(0, 10);//精确到秒
		return time;
	}


	//向服务器上报进度
	callAppToSpeed() {
		let url = this.ReportType.report_speed
		let params = {
			"userId": this.userId,
			"type": this.moduleId,
			"speedVal": 2,
			"lessonModelId": this.lessonModuleId,
			"packageId": this.packageId,
			"nextLessonModelId": this.nextLessonModelId,
			"nextType": this.nextType
		}
		this.callAppToHttp(url, "POST", params)
	}

	getNative(handleData) {
		//ios
		if (window.tiGo) {
			window.tiGo.callApp(JSON.stringify(handleData));
		}
		//android
		if (window.webkit) {
			window.webkit.messageHandlers.callApp.postMessage(JSON.stringify(handleData));
		}
	}

	/**
	 * 埋点
	 * @param event 事件ID
	 * @param data 事件信息
	 */
	callPoint(event: string, data: any) {
		var handleData =
		{
			"action": this.CocosByNativeAction.TiGoAnalyse,
			"param": {
				"analyseAction": "logEvent",
				"analyseParam": {
					"analyseId": event,
					"analyseData": data
				}
			}
		}
		this.getNative(handleData)
		console.log("callAppToLoaded ============== " + JSON.stringify(handleData));
	}
}
//注册app访问cocos的全局函数
//msg1 : 具体内容跟app协商
window.callCocos = function (msg1) {
	if (msg1.action == "TiGoLifecycle") {
		window.webframe.Notify.trigger("callCocos_loaded");
	} else {
		window.webframe.Notify.trigger("callCocos_data", msg1);
	}

	return "callByAndroidParam ok!";
};

export default TigoManager.getInstance();